import { createApp } from 'vue'
import App from './App.vue'

import '@/assets/styles/main.scss'

// 4.8 В Main.js импортируем обьект language из index.js
/* Импортируем languages С index.js */
import { languages } from '@/i18n';

/* 4.9 Импортируем createI18n из vue-i18n в main.js
   Импортируем функцию с инструмента vue-i18n */
import { createI18n } from 'vue-i18n';

/* 4.10 В main.js Создать переменную localeStorageLang Где будем хранить значение lang из localStorage  */
const localeStorageLang = localStorage.lang;

/* 4.11) Создать переменную i18n и в ней вызвать функцию createI18n и туда передать обьект
         Создаём экспрейшн функцию в которую передаём обьект с настройками  */
// 4.12 Описать тот обьект который передаём в функцию vue-i18n         
const i18n = createI18n(
    {
        /* Этот ключ нужен для того чтобы vue3 понимал и корректно воспринемал функцию vue-i18n  */
        legacy: true,
        /* В ключ ниже передаётся значени с переменной если там ничего нет, то по умолчанию будет русский язык */
        locale: localeStorageLang || 'ru',
        /* В этот ключ передаём наш обьект languages */
        messages: languages
    }
)

// 4.13  Сказать нашему Vue чтобы он использовал его при созданий приложения
//       use(i18n)
createApp(App).use(i18n).mount('#app') 
